*,
*::before,
*::after {
	box-sizing: border-box;
	padding: 0;
	margin: 0;
}

html {
	overflow-y: scroll;
	scroll-behavior: smooth;
}

html,
body {
	width: 100%;
	overflow-x: hidden;
	font-family: 'Spoqa Han Sans Neo', sans-serif;
}

img {
	height: auto;
	max-width: 100%;
	vertical-align: bottom;
}

ul {
	margin: 0;
	padding: 0;
}

li {
	list-style: none;
}

a {
	text-decoration: none;
	color: inherit;
	cursor: pointer;
}

button {
	background-color: transparent;
	color: inherit;
	border-width: 0;
	padding: 0;
	cursor: pointer;
}

input::-moz-focus-inner {
	border: 0;
	padding: 0;
	margin: 0;
}

input[type='text'],
input[type='password'],
input[type='email'],
input[type='search'],
input[type='submit'],
input[type='tel'],
input[type='url'],
[type='textarea'],
textarea {
	border-radius: 0;
	-webkit-appearance: none;
	outline: none;
}

input[type='text'],
input[type='password'] {
	padding-left: 10px;
}
